/**
 * Mixin
 */
export const petMixin = {
  methods: {
    calculateEvolSkillList(pet) {
      const evol_skill_list = []

      if (!pet.EvolSkill || !window.CBG_GAME_CONFIG.pet_skill_high_to_other_level_mapping) {
        return evol_skill_list
      }

      // ID
      const cifu = pet.EvolSkill.split('|').map(Number)
      const cifuObj = {}
      cifu.forEach(function (number) {
        cifuObj[number] = 1
      })

      const evol_skills = cifuObj
      if (!evol_skills) {
        return evol_skill_list
      }

      const evol_skill_str = []
      for (const typeid in evol_skills) {
        evol_skill_str.push('' + typeid)
      }

      for (let i = 0, max = evol_skill_str.length; i < max; i++) {
        const typeid = evol_skill_str[i]
        const evol_skill_hash = window.CBG_GAME_CONFIG.pet_skill_high_to_other_level_mapping[typeid]

        if (!evol_skill_hash) {
          continue
        }

        const evol_skill_item = {
          skill_type: typeid,
          level: evol_skills[typeid],
          evol_type: typeid
        }

        if (pet.all_skills) {
          const isHighSkill = pet.all_skills[String(evol_skill_hash.high_skill)]
          const isLowSkill = pet.all_skills[String(evol_skill_hash.low_skill)]

          if (isHighSkill || isLowSkill) {
            evol_skill_item.hlightLight = true
            if (!isHighSkill) {
              evol_skill_item.evol_type = evol_skill_hash.low_skill
            }
          } else {
            evol_skill_item.hlightLight = false
          }
        }
        evol_skill_list.push(evol_skill_item)
      }

      return evol_skill_list
    },
    /**
     * URL
     * @param {number} skillId - ID
     * @returns {string} URL
     */
    getSkillImage(skillId = 0) {
      if (skillId === 0) {
        return ''
      }
      // skillId40
      const paddedId = skillId.toString().padStart(4, '0')
      return `https://cbg-xyq.res.netease.com/images/skill/${paddedId}.gif`
    },

    /**
     * 
     * @param {string} desc - 
     * @returns {Object} 
     */
    parsePetInfo(desc) {
      const pet_desc = window.parse_desc_info(desc)
      const newLinliData = this.parse_fashang_fafang(desc)
      const pet_attrs = window.get_pet_attrs_info(pet_desc, {
        only_basic_attr: false,
        fashang: newLinliData.fashang,
        fafang: newLinliData.fafang
      })
      return pet_attrs
    },

    /**
     * 
     * @param {string} desc_info - 
     * @returns {Object} 
     */
    parse_fashang_fafang(desc_info) {
      try {
        const data = JSON.decode(window.decode_desc(desc_info)) // 
        return {
          fashang: data.fashang,
          fafang: data.fafang
        }
      } catch (e) {
        return {}
      }
    },

    /**
     * 
     * @param {Object} pet - 
     * @returns {string} HTML
     */
    formatSkills({ petData },row=2,col=8) {
      try {
        // 
        const tempContainer = document.createElement('div')

        // 
        const { pet_tip_skill_grid: result } = window.show_pet_skill_in_grade(
          petData.all_skill,
          petData.sp_skill,
          row,
          col,
          {
            pet_skill_url: 'https://cbg-xyq.res.netease.com/images/skill/',
            notice_node_name: 'pet_tip_notice_msg',
            skill_panel_name: 'pet_tip_skill_grid',
            enhance_skills: [],
            table_class: 'tb03'
          },
          petData
        )
        // DOM
        result.forEach((node) => {
          if (node) {
            tempContainer.appendChild(node)
          }
        })
        // HTMLtd
        return tempContainer.innerHTML
      } catch (error) {
        console.error(':', error)
        return ''
      }
    },
    /**
     * 
     * @param {Object} pet - 
     * @returns {Object} 
     */
    getEnhanceInfo(pet) {
      var equip_display_conf = {
        // 
        pet: { skill_id_list: [571, 661], is_baobao: 1 },
        search: { is_hide_unreasonable_price_equips: 1 }
      }
      var enhanceInfo = equip_display_conf.pet || {}
      var time_lock = pet.is_time_lock
      time_lock = time_lock > 0 || time_lock == 'true'
      var time_lock_days
      if (time_lock) {
        time_lock_days = pet.time_lock_days
      }
      enhanceInfo.time_lock = time_lock
      enhanceInfo.time_lock_days = time_lock_days
      return {
        time_lock: pet.time_lock || false,
        time_lock_days: pet.time_lock_days || 90,
        is_baobao: pet.petData.is_baobao === '' || false,
        skill_id_list: enhanceInfo.skill_id_list || []
      }
    },

    /**
     * 
     * @param {string} allSkill - 
     * @param {Object} targetPet - 
     * @returns {string} 
     */
    formatSkillsDiff(allSkill, targetPet) {
      if (!allSkill || !targetPet || !targetPet.all_skill) return ''

      const targetPetSkill = targetPet.all_skill

      // 
      const targetSkills = targetPetSkill.split('|').filter((skill) => skill.trim())
      const currentSkills = allSkill.split('|').filter((skill) => skill.trim())

      // 
      const diffSkills = currentSkills.filter((skill) => !targetSkills.includes(skill))

      // 
      if (diffSkills.length > 0) {
        return (
          '+' +
          diffSkills
            .map((skillId) => {
              return window.CBG_GAME_CONFIG.xyq_pet_skills[skillId]
            })
            .join('|')
        )
      }

      // 
      return ''
    },

    /**
     * 
     * @param {string} texing - JSON
     * @returns {string} 
     */
    formatTexing(texing) {
      if (!texing) return ''
      try {
        const texingObj = JSON.parse(texing)
        return texingObj.name || ''
      } catch (e) {
        return ''
      }
    }
  }
}
