import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/HomeView.vue'
import AutoParams from '@/components/AutoParams.vue'
Vue.use(VueRouter)

const routes = [

  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/auto-params',
    name: 'AutoParams',
    component: AutoParams,
    meta: {
      title: '',
      requiresAuth: true
    }
  },
  {
    path: '/roles/:type/:levelRange/:page?',
    name: 'RoleList',
    component: () => import('../views/RoleList.vue'),
    meta: {
      title: '',
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/equipments',
    name: 'EquipmentList',
    component: () => import('../views/EquipmentList.vue'),
    meta: {
      title: '',
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/equipments/:equip_sn',
    name: 'EquipmentDetail',
    component: () => import('../views/EquipmentDetail.vue'),
    meta: {
      title: '',
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/pets',
    name: 'PetList',
    component: () => import('../views/PetList.vue'),
    meta: {
      title: '',
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/equipment-desc-creator',
    name: 'EquipmentDescCreator',
    component: () => import('../views/EquipmentDescCreator.vue'),
    meta: {
      title: '',
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/market-data-status',
    name: 'MarketDataStatus',
    component: () => import('../views/MarketDataStatus.vue'),
    meta: {
      title: '',
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/admin/users',
    name: 'UserManagement',
    component: () => import('../views/UserManagement.vue'),
    meta: {
      title: '',
      showHeader: true,
      requiresAuth: true
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue'),
    meta: {
      title: '',
      showHeader: false
    }
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title || ''
  
  // 
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 
    const token = localStorage.getItem('auth_token')
    const userInfoStr = localStorage.getItem('user_info')
    
    if (!token || !userInfoStr) {
      // 
      console.log(':', to.fullPath)
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      })
    } else {
      // 
      try {
        const userInfo = JSON.parse(userInfoStr)
        if (userInfo && userInfo.username) {
          // 
          if (!userInfo.is_admin) {
            // 
            console.warn('')
            localStorage.removeItem('auth_token')
            localStorage.removeItem('user_info')
            next({
              path: '/login',
              query: { redirect: to.fullPath }
            })
            return
          }
          // 
          next()
        } else {
          // 
          console.warn('')
          localStorage.removeItem('auth_token')
          localStorage.removeItem('user_info')
          next({
            path: '/login',
            query: { redirect: to.fullPath }
          })
        }
      } catch (error) {
        // 
        console.error(':', error)
        localStorage.removeItem('auth_token')
        localStorage.removeItem('user_info')
        next({
          path: '/login',
          query: { redirect: to.fullPath }
        })
      }
    }
  } else {
    // 
    next()
  }
})

export default router
